import React, { useContext } from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import { useTrail, animated } from 'react-spring'
import ProjectPreview from './projectPreview'
import Styles from './project-preview.module.css'
import VisibilityContext from '../../context/visibility'

const Projects = () => {
	const bodyVisibility = useContext(VisibilityContext)
	const {
		allProjectsJson: { nodes: projects },
	} = useStaticQuery(
		graphql`
			query {
				allProjectsJson {
					nodes {
						id
						name
						description
						year
						thumbnail {
							src {
								childImageSharp {
									fluid(maxWidth: 600, maxHeight: 352) {
										...GatsbyImageSharpFluid_withWebp
									}
								}
							}
						}
					}
				}
			}
		`
	)
	const trail = useTrail(projects.length, {
		opacity: bodyVisibility ? 1 : 0,
		config: { mass: 5, tension: 2000, friction: 200, clamp: true },
		transform: `translateY(${bodyVisibility ? '0px' : '25px'})`,
	})
	return (
		<section className={`home-section ${Styles.section}`}>
			<div className="container container--wide home__flexwrap">
				<header className={`home__header ${Styles.body}`}>
					<h2>Recent Projects</h2>
				</header>

				<div className="home__body">
					<ul
						className={`list-unstyled ${Styles.list} ${Styles.noGutter}`}
					>
						{trail.map((props, i) => (
							<animated.li
								style={props}
								key={projects[i].id}
								className={Styles.project}
							>
								<ProjectPreview
									id={projects[i].id}
									name={projects[i].name}
									thumbnail={projects[i].thumbnail}
								/>
							</animated.li>
						))}
						{/* {projects.map(project => {
                            return (
                                <ProjectPreview
                                    key={project.id}
                                    id={project.id}
                                    name={project.name}
                                    thumbnail={project.thumbnail}
                                />
                            )
                        })} */}
					</ul>
				</div>
			</div>
		</section>
	)
}

export default Projects
